/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  border: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline;
}

html {
  -webkit-font-smoothing: none;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  overflow-x: hidden;
}

body {
  margin: 0;
  overflow-x: hidden;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

a:active {
  outline: 0;
}

figure {
  margin: 0;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

input[type=search], input[type=text], input[type=password] {
  padding: 8px;
  flex-grow: 1;
  min-width: 3em;
  border: 1px solid #949494;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}

input[type=submit],
button[type=submit] {
  background: #f7f7f7;
  border: 1px solid #ccc;
  padding: 8px 0.625em;
  color: #32373c;
  margin-left: 0.625em;
  word-break: normal;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  cursor: pointer;
}

.admin-bar .md-shockerhitch-header {
  top: 32px;
}
@media (max-width: 767px) {
  .admin-bar .md-shockerhitch-header {
    top: 46px;
  }
}
.admin-bar .md-shockerhitch-header.fixed-header {
  top: 32px;
}
@media (max-width: 767px) {
  .admin-bar .md-shockerhitch-header.fixed-header {
    top: 0;
  }
}

.md-shockerhitch-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 200;
  transition: all 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);
}
.md-shockerhitch-header .site-title {
  font-size: 26px;
  font-weight: 700;
}
.md-shockerhitch-header.fixed-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.md-shockerhitch-header .wp-block-search {
  width: 300px;
}
.md-shockerhitch-header .wp-block-search .wp-block-search__label {
  display: none;
}
.md-shockerhitch-header .wp-block-search .wp-block-search__input {
  padding-right: 10px;
  font-size: 14px;
  background: #fff;
  padding-left: 20px;
  border: 1px solid #000;
}
.md-shockerhitch-header .wp-block-search button {
  margin-left: 0;
  font-size: 0px;
  padding: 0 15px;
}
.md-shockerhitch-header .wp-block-search button::before {
  content: "\f179";
  font-family: dashicons;
  font-size: 20px;
}
.md-shockerhitch-header .wp-block-navigation__responsive-container li.has-child ul li a {
  color: #151515;
  border-bottom: 1px solid #e9ecef;
  background: #fff;
  padding: 15px 30px;
  width: 100%;
  line-height: normal;
  font-weight: 600;
  text-decoration: none;
}
.md-shockerhitch-header .wp-block-navigation__responsive-container li.has-child ul li a:hover {
  background: #011dfe;
  color: #fff !important;
}

@media (min-width: 768px) {
  .md-shockerhitch-menu ul {
    position: relative;
    text-align: right;
  }
  .md-shockerhitch-menu ul li {
    position: relative;
    display: inline-block;
    margin-right: 30px;
  }
  .md-shockerhitch-menu ul li a {
    font-size: 15px;
    font-weight: 500;
    line-height: 26px;
    position: relative;
    z-index: 999;
  }
  .md-shockerhitch-menu ul li .children,
  .md-shockerhitch-menu ul li .sub-menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    display: none;
    float: left;
